@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
    background-color: black;
    color: white;
    font-family: 'Nunito',sans-serif;
}

.widgetWrap{
    background: #2a2a2a;
    height: 182px;
    margin-bottom: 19px;
}

.widgetTitle{
    border-bottom: 1px solid #665151;
    text-align: left;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 600;
    padding: 10px 10px;
    color: #b6b6e3;
}

.widgetValue{
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    align-items: middle ;
    height: 143px;
    padding-left: 7px;
}

.widgetValue .value{
     font-size: 56px;
     font-weight: 700;
     text-align: left;
     padding: 0px 7px;
     line-height: 100%;
}

.description{
    text-align: left;
    font-size: 12px;
    padding-left: 13px;
}

text{
    fill: white;
}

.TopHeader.row{
    background: #2a2a2a;
    margin-bottom: 19px;
    padding: 10px 5px;
    font-size: 24px1;
    text-align: left;
    font-weight: bold;
}

.Dropdown-root{
    font-size: 15px;
    width: 142px;
    float: right;
    cursor: pointer;
}

.value{
    color: yellow;
}
